@use 'variables' as v;

/** Media Queries **/

@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    margin-top: 2rem;
    font-size: 2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    width: 100%;
    margin: 5rem 0;
    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( $bgColor, 10%);
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton-principal() {
    background-color: v.$principal;
    padding: 1rem 2rem;
    color: v.$blanco;
    font-size: 2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border: none;
    //border-radius: 1rem;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    margin: 2rem 0;
    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( v.$principal, 10%);
        cursor: pointer;
    }
}

@mixin boton-transparente() {
    background-color: transparent;
    padding: 1rem 2rem;
    color: v.$negro;
    font-size: 2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    margin: 2rem 0;
    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( v.$principal, 10%);
        color: v.$blanco;
        cursor: pointer;
    }
}