.container-small {
    width: 50%;
    margin: auto;
}

.text-center {
    text-align: center;
}

.text-primary {
    color: $principal;
}

.formulario {
    form {
        width: 100%;
        padding: 2rem 2rem;
        display: flex;
        flex-direction: column;
    }
    label {
        margin-bottom: 15px;
        position: relative;
        border-bottom: 1px solid #ddd;
    }
    input {
        width: 100%;
        padding: 10px 0px;
        margin-top: 20px;
        border: none;
        outline: none;
    }
    input::placeholder {
        opacity: 0;
    }
    span {
        position: absolute;
        top: 0;
        left: 0;
        transform: translateY(30px);
        font-size: 0.825em;
        transition-duration: 300ms;
    }
    label:focus-within>span,
    input:not(:placeholder-shown)+span {
        color: $principal;
        transform: translateY(0px);
    }
}

.btn-principal {
    border: none;
    background-color: $principal;
    border-radius: 0.5rem;
    padding: 0.5rem 0;
    color: white;
}

.btn-principal:hover {
    cursor: pointer;
    background-color: darken($principal, 8);
}

.btns-auxiliares {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: auto;
}

.btn-secundario {
    border: none;
    background-color: $principal-1;
    border-radius: 0.5rem;
    padding: 0.5rem 0;
    color: white;
    padding: 1rem 2rem;
}

.btn-secundario:hover {
    cursor: pointer;
    background-color: darken($principal-1, 8);
}

.error {
    background-color: lighten($rojo, 47);
    border-left: 5px solid $rojo;
    text-align: center;
    padding: 0.2rem 0;
    margin: 1rem 2rem;
    font-weight: $bold;
}

.successful {
    background-color: lighten($verde, 47);
    border-left: 5px solid $verde;
    padding: 0.2rem 0;
    text-align: center;
    margin: 1rem 2rem;
    font-weight: $bold;
}

.btn-volver {
    border: none;
    background-color: $gris;
    border-radius: 0.5rem;
    padding: 0.5rem 1rem;
}

.btn-volver:hover {
    cursor: pointer;
    background-color: darken($gris, 10);
}