h1,
h2,
h3 {
  font-family: "Courgette", cursive;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

label {
  font-size: "Ubuntu", sans-serif;
}

.container-small {
  width: 50%;
  margin: auto;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: #2D572C;
}

.formulario form {
  width: 100%;
  padding: 2rem 2rem;
  display: flex;
  flex-direction: column;
}
.formulario label {
  margin-bottom: 15px;
  position: relative;
  border-bottom: 1px solid #ddd;
}
.formulario input {
  width: 100%;
  padding: 10px 0px;
  margin-top: 20px;
  border: none;
  outline: none;
}
.formulario input::placeholder {
  opacity: 0;
}
.formulario span {
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(30px);
  font-size: 0.825em;
  transition-duration: 300ms;
}
.formulario label:focus-within > span,
.formulario input:not(:placeholder-shown) + span {
  color: #2D572C;
  transform: translateY(0px);
}

.btn-principal {
  border: none;
  background-color: #2D572C;
  border-radius: 0.5rem;
  padding: 0.5rem 0;
  color: white;
}

.btn-principal:hover {
  cursor: pointer;
  background-color: #1f3c1e;
}

.btns-auxiliares {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: auto;
}

.btn-secundario {
  border: none;
  background-color: #51135E;
  border-radius: 0.5rem;
  padding: 0.5rem 0;
  color: white;
  padding: 1rem 2rem;
}

.btn-secundario:hover {
  cursor: pointer;
  background-color: #340c3c;
}

.error {
  background-color: #ffbcbc;
  border-left: 5px solid #cb0000;
  text-align: center;
  padding: 0.2rem 0;
  margin: 1rem 2rem;
  font-weight: 700;
}

.successful {
  background-color: #b3ff90;
  border-left: 5px solid #329f00;
  padding: 0.2rem 0;
  text-align: center;
  margin: 1rem 2rem;
  font-weight: 700;
}

.btn-volver {
  border: none;
  background-color: #cfcfcf;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
}

.btn-volver:hover {
  cursor: pointer;
  background-color: #b6b6b6;
}

.fondo-html {
  background-image: url("/img/3 cortes.webp");
}

.ventana {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 20rem;
  border-radius: 0.5rem;
  padding: 7rem 0;
  background-color: white;
}

.img-ventana {
  margin: auto;
}
.img-ventana img {
  border-radius: 0.5rem;
}

.container-login {
  margin: 0rem 1rem;
}

.navegacion {
  background-color: #2D572C;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}